.partners{
  overflow: hidden;
  &:before{
    background-image: url(../img/partners-bg.webp);
  }
  &__top{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 1.25em 2em;
  }
  &__items{
    margin-top: 1.56em;
    @include grid100gap(1.875em);
  }
}

.partner-slider{
  &:nth-child(odd){
    transform: translateX(-4em);
  }
  .swiper{
    &-wrapper{
      -webkit-transition-timing-function: linear !important;
      transition-timing-function: linear !important;
    }
    &-slide{
      width: 13.78em;
      padding-right: 1em;
    }
  }
}

.partner-card{
  @include center;
  border-radius: 1.25em;
  border: 1px solid $grey50;
  height: 6.5625em;
  padding: .5em 2.5em;
  &:hover{
    border-color: $primary;
  }
  img{
    max-width: 100%;
    max-height: 100%;
  }
}

.reviews{
  &__title{
    text-align: center;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3em;
  }
}

.reviews-slider{
  margin-top: 1.875em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - .5em));
        gap: 1em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
      }
    }
    &:not(._show-all){
      .swiper-slide:nth-child(4)~.swiper-slide{
        display: none;
      }
    }
  }
}

.review-card{
  padding: 1.875em;
  background-color: #fff;
  border-radius: .625em;
  &__top{
    display: flex;
    align-items: center;
  }
  .avatar{
    margin-right: 1em;
  }
  &__prof{
    opacity: 0.4;
  }
  &__text{
    margin-top: 1.25em;
    .text18{
      @include truncate(6);
    }
  }
}

.avatar{
  width: 4.5em;
  height: 4.5em;
  flex-shrink: 0;
  border-radius: 50%;
  background: #F8F4EF;
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
  }
}

.subscribe{
  &__inner{
    padding: 4.44em 6em 0;
    border-radius: .625em;
    background: $gradientDesktop;
    display: grid;
    grid-template-columns: calc(100% - 37em) 30.6875em;
    justify-content: space-between;
    color: #fff;
    overflow: hidden;
    &:before{
      background-image: url(../img/subscribe-bg.webp);
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
  }
  &__sidebar{
    padding-bottom: 4.5em;
    position: relative;
    z-index: 2;
  }
  &__text{
    @include grid100gap(1.25em);
  }
  &__desc{
    opacity: 0.62;
  }
  &__img{
    margin: auto 0 0;
    width: 24em;
    max-width: 100%;
    padding-top: .44em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.subscribe-form{
  .fg{
    input{
      border-color: rgba(255, 255, 255, 0.20);
      background: rgba(255, 255, 255, 0.16);
      color: #fff;
      &::placeholder{
        color: #fff;
      }
    }
  }
  &__fields{
    @include grid100gap(1em);
  }
  &__btn{
    height: 3.5em;
    width: 100%;
    margin-top: 2.5em;
    &:not(:hover){
      background: $black;
    }
  }
  &__hint{
    max-width: 27.5em;
    margin-top: 1.5em;
    opacity: 0.8;
    font-weight: 500;
  }
}

.about{
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 3.5em / 3));
    gap: 1.5em 1.75em;
  }
}

.about-card,
.about-card2,
.about-card3{
  color: #fff;
  &:before{
    display: none;
  }
  .text{
    margin-top: 1.25em;
  }
}

.about-card{
  overflow: hidden;
  padding: 2.44em 2.5em 0 1.5em;
  background: $gradientDesktop;
  border-radius: 1.25em 1.25em 12.5em 1.25em;
  &__img{
    padding-top: 1.875em;
    margin-top: auto;
    img{
      width: 100%;
      display: block;
    }
  }
}

.about-card2{
  grid-column: 2/3;
  grid-row: 1/3;
  border-radius: 1.25em;
  background-color: $yellow;
  &__bottom{
    padding: 2.81em 2.63em 1em 1.25em;
  }
  .about-card__title{
    max-width: 15em;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.25em 1.25em 12.5em 1.875em;
  }
}

.about-card3{
  overflow: hidden;
  background-color: $blue;
  padding: 1.44em 1.82em 0;
  border-radius: 1.25em 1.25em 1.25em 12.5em;
  &__img{
    width: 15.6875em;
    max-width: 100%;
    margin-left: auto;
    margin-top: -3.3em;
    img{
      display: block;
      width: 100%;
    }
  }
}

.about-card4{
  &__img{
    display: block;
    width: 100%;
    border-radius: 12.5em 1.25em 1.25em 1.25em;
  }
}

.about-card5{
  &__img{
    display: block;
    width: 100%;
    border-radius: 1.25em 12.25em 1.25em 1.25em;
  }
}

.text{
  font-weight: 500;
  li,
  p{
    font-size: 1.125em;
    line-height: 155%;
  }
  ul{
    @include grid100gap(1em);
    li{
      padding-left: 2.22em;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
        width: 1.33em;
        height: 1.33em;
        background-image: url(../img/done.svg);
        @include bgImgSize(100%);
      }
    }
  }
}


@media screen and (max-width: 992px){
  .partners{
    &:before{
      background-image: url(../img/partners-bg2.webp);
    }
    &__top{
      grid-template-columns: 100%;
    }
    &__items{
      margin-top: 1.25em;
      gap: 1.0625em;
    }
  }

  .partner-slider{
    .swiper{
      &-slide{
        width: 12.0625em;
        padding-right: 1em;
      }
    }
  }

  .partner-card{
    border-radius: .625em;
    height: 5.125em;
    padding: .5em 1.25em;
    img{
      max-height: 3.1875em;
    }
  }

  .reviews{
    overflow: hidden;
    &__nav{
      display: none;
    }
  }

  .reviews-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 20.6875em;
        padding-right: 1em;
      }
    }
  }

  .review-card{
    padding: 3.4em 1.25em;
    .avatar{
      width: 3.5em;
      height: 3.5em;
      margin-right: .625em;
    }
    &__text{
      margin-top: 1em;
      .text18{
        -webkit-line-clamp: 12;
      }
    }
  }

  .subscribe{
    &__inner{
      padding: 1.875em 1.25em 0;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__sidebar{
      padding-bottom: 1em;
    }
    &__text{
      gap: .625em;
    }
    &__img{
      width: 18em;
    }
  }

  .subscribe-form{
    &__fields{
      gap: .625em;
    }
    &__btn{
      margin-top: 1em;
      height: 2.75em;
    }
    &__hint{
      margin-top: .375em;
    }
  }

  .about{
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      margin-top: 1.25em;
    }
  }

  .about-card,
  .about-card2,
  .about-card3{
    padding: 1.25em;
    min-height: 14em;
    border-radius: .625em;
    &:before{
      display: block;
      background-position: right top;
    }
    .text,
    &__title{
      position: relative;
      z-index: 2;
    }
    .text{
      margin-top: .5em;
    }
    &__img{
      display: none;
    }
  }

  .about-card{
    &:before{
      background-image: url(../img/about-bg.webp);
    }
    &__title{
      font-size: 1.125em;
      line-height: 155%;
      max-width: 90%;
    }
  }

  .about-card2{
    grid-column: unset;
    grid-row: unset;
    &:before{
      background-image: url(../img/about-bg2.webp);
    }
    &__bottom{
      padding: 0;
    }
    .about-card__title{
      max-width: 90%;
    }
  }

  .about-card3{
    &:before{
      background-image: url(../img/about-bg3.webp);
    }
  }

  .about-card4,
  .about-card5{
    display: none;
  }

  .text{
    ul{
      gap: .5em;
    }
    li,
    p{
      font-size: 1em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .reviews{
    &__title{
      text-align: left;
    }
  }

  .subscribe{
    &__inner{
      grid-template-columns: 100%;
      padding-bottom: 1.875em;
      background: $gradientMob;
      &:before{
        background-image: url(../img/subscribe-bg2.webp);
      }
    }
    &__desc{
      opacity: 0.84;
    }
    &__img{
      padding: 0;
      width: 5.625em;
      position: absolute;
      right: -1.25em;
      top: -1.75em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .about{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
