.footer{
  padding: 4.5em 0 4.13em;
  border-top: 1px solid $stroke;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logos{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    width: 2.875em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__partner{
    margin-right: .625em;
    img{
      max-width: 4.3125em;
      max-height: 2.9375em;
      display: block;
    }
  }
  .socials{
    margin-top: 1.25em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2.5em;
  }
  &__link{
    font-weight: 500;
    opacity: 0.5;
    @include lastM(0 0 .75em);
    &:hover{
      opacity: 1;
    }
    span{
      font-size: 0.875em;
      line-height: 143%;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.25em;
    flex-shrink: 0;
    @include lastM(0 .625em 0 0);
    &:hover{
      img{
        transform: translateY(-20%);
      }
    }
    img{
      transition: all .3s;
      width: 100%;
      display: block;
    }
  }
}

.footer-action{
  display: none;
}

.footer-nav{
  max-width: 18.75em;
  &__title{
    margin-bottom: 1em;
  }
  &__links{
    display: flex;
    flex-direction: column;
  }
  &__link{
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    @include lastM(0 0 .75em);
  }
  @media screen and (min-width: 577px){
    &__hidden{
      display: block !important;
    }
    ._hide-desktop{
      display: none;
    }
  }
}

.footer-contacts{
  @include grid100gap(1.5em);
  &__desc{
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 2.5em 0;
    &__info,
    &__inner{
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 2em 1.5em;
    }
    &__info{
      grid-column: 1/-1;
    }
    .socials,
    &__links{
      margin: 0;
    }
  }

  .footer-nav{
    max-width: 100%;
    &__title{
      margin-bottom: .625em;
    }
    &__link{
      @include lastM(0 0 .625em);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 1.25em 0;
    &__inner{
      position: relative;
      padding-bottom: 7.75em;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__info{
      grid-template-columns: 100%;
      gap: .625em;
      margin-bottom: .875em;
    }
    &__link{
      @include lastM(0 0 .625em);
    }
    &__bottom{
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }

  .socials{
    &__item{
      width: 2em;
      @include lastM(0 .5em 0 0);
    }
  }

  .footer-action{
    display: flex;
    align-items: center;
    margin-bottom: 1.25em;
    .lang{
      margin-right: 1em;
      .m-dropdown{
        top: unset;
        bottom: 100%;
        margin: 0 0 .5em;
      }
    }
    &__view{
      width: 1.5em;
      @include center;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          fill: $black;
        }
      }
    }
  }

  .footer-nav{
    padding: 1em 0;
    border-bottom: 1px solid #E4E0D9;
    &._last-mob{
      order: 2;
    }
    &._open{
      .footer-nav__show{
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
    &__hidden{
      display: none;
      margin-top: .625em;
    }
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .footer-nav__title{
        margin-bottom: 0;
      }
      &:after{
        content: '';
        flex-shrink: 0;
        background-image: url(../img/arrow-bottom.svg);
        @include bgImgSize(100%);
        width: 1.5em;
        height: 1.5em;
        margin-left: 1em;
        transition: all .3s;
      }
    }
    &__title{
      .text18{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }
}
