.team{
  padding: 1.875em 0 5.31em;
}

.filter{
  display: flex;
  .m-search{
    flex-grow: 1;
  }
  .filter-select{
    margin-left: 1em;
  }
}

.m-search{
  position: relative;
  &._show{
    .m-search__clear{
      opacity: 1;
      pointer-events: unset;
    }
    .m-search__result{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    position: relative;
  }
  .fg{
    input{
      background-color: $grey10;
      padding-left: 4em;
      padding-right: 4em;
      height: 4em;
      background-image: url(../img/search-icon.svg);
      background-repeat: no-repeat;
      background-size: 1.5em auto;
      background-position: left 1.25em center;
      &::placeholder {
        color: $grey80;
      }
    }
  }
  &__clear{
    position: absolute;
    background-image: url(../img/clear.svg);
    @include bgImgSize(100%);
    width: 1.5em;
    height: 1.5em;
    right: 1.25em;
    top: 1.25em;
    opacity: 0;
    pointer-events: none;
  }
  &__result{
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .625em;
    box-shadow: 0px -1px 36px 0px rgba(240, 236, 227, 0.84);
    border-radius: 1.875em;
    background-color: #fff;
    z-index: 3;
    max-height: 21.25em;
    overflow-y: auto;
    @include scroll;
  }
  &__item{
    display: flex;
    align-items: center;
    min-height: 4.25em;
    padding: .5em 1.25em;
    &:hover{
      color: $primary10;
      background-color: $primary;
    }
  }
}

.filter-select{
  flex-shrink: 0;
  position: relative;
  &._open{
    .m-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
    .filter-select__show{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__show{
    height: 4em;
    display: flex;
    align-items: center;
    padding: 0 1.5em;
    border-radius: 12.5em;
    background-color: $black;
    color: #fff;
    font-weight: 500;
    cursor: pointer;
    svg{
      width: 1.5em;
      height: auto;
      margin-left: .5em;
    }
  }
  &__current{
    font-size: 1em;
    line-height: 150%;
  }
  .m-dropdown{
    left: unset;
    right: 0;
    min-width: 27em;
  }
}

.team-group{
  margin-top: 2.5em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3em / 4));
    gap: 1em;
  }
}

.team-card{
  position: relative;
  &__side{
    background: $grey20;
    border-radius: 0.625em;
    padding: 1.25em;
    height: 25.375em;
    transition: transform .6s cubic-bezier(.2,.85,.4,1.275);
    backface-visibility: hidden;
  }
  &__side1{
    display: flex;
    align-items: flex-end;
    position: relative;
    color: #fff;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(180deg, rgba(127, 110, 90, 0.00) 55.93%, #81725F 100%);
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      z-index: 2;
    }
  }
  &__img{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 0 .25em;
    max-height: 100%;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__content{
    position: relative;
    z-index: 3;
    @include grid100gap(.375em);
  }
  &__side2{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    transform: rotateY(-180deg);
    display: flex;
    flex-direction: column;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  .avatar{
    width: 4em;
    height: 4em;
    background-color: $primary80;
    margin-right: .75em;
  }
  &__text{
    margin-top: .5em;
    .text14{
      @include truncate(6);
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 2em;
  }
  &__items{
    @include grid100gap(.5em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    span{
      font-size: .875em;
      line-height: 143%;
    }
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-left: 1em;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.875em;
    height: 3em;
  }
  &._flip-over{
    ._back{
      transform: rotateY(0deg);
      z-index: 2;
    }
    ._front{
      transform: rotateY(180deg);
      z-index: -1;
    }
  }
}

.vacancies{
  overflow: hidden;
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
}

.vacancies-slider{
  margin-top: 2.69em;
  position: relative;
  .swiper{
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.5em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 34.0625em;
      height: auto;
      padding-right: 1em;
    }
  }
}

.vacancy-card{
  padding: 1.875em;
  border-radius: 0.625em;
  background-color: $bg;
  display: flex;
  flex-direction: column;
  min-height: 18.625em;
  height: 100%;
  &__items{
    margin-top: .625em;
    display: flex;
    align-items: center;
  }
  &__item{
    font-weight: 500;
    display: flex;
    align-items: center;
    @include lastM(0 1.25em 0 0);
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__text{
    margin-top: 1.25em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
    display: flex;
  }
}

.search-empty{
  margin: 0 auto;
  padding: 2em 0;
  max-width: 24.25em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  &__img{
    width: 10em;
  }
  &__info{
    @include grid100gap(.75em);
  }
  &__desc{
    opacity: .6;
    line-height: 155%;
  }
}


@media screen and (max-width: 992px){
  .team{
    padding-bottom: 2.5em;
  }

  .filter{
    .filter-select{
      margin-left: .5em;
    }
  }

  .m-search{
    .fg{
      input{
        font-size: 1em;
        height: 3.25em;
        padding: 0 4em;
        background-size: 1.25em auto;
      }
    }
    &__clear{
      top: .875em;
    }
    &__items{
      padding: .625em 0;
    }
    &__item{
      min-height: 3.25em;
    }
  }

  .filter-select{
    &__show{
      width: 3.25em;
      height: 3.25em;
      padding: 0;
      background-image: url(../img/filter.svg);
      @include bgImgSize(1.25em);
      svg{
        display: none;
      }
    }
    &__current{
      display: none;
    }
    .m-dropdown{
      min-width: auto;
      width: calc(100vw - 2.5em);
    }
  }

  .team-group{
    margin-top: 2em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    }
  }

  .team-card{
    &__side{
      padding: 1em;
    }
  }

  .vacancies{
    &__nav{
      margin-top: 1.875em;
    }
  }

  .vacancies-slider{
    margin-top: 1.25em;
    .swiper{
      &-nav{
        display: none;
      }
      &-slide{
        width: 19.125em;
      }
    }
  }

  .vacancy-card{
    padding: 1.25em;
    min-height: 18.125em;
  }

  .search-empty{
    padding: 1.875em 0;
    max-width: 21em;
    &__img{
      width: 7.5em;
    }
    &__info{
      gap: .625em;
    }
    &__desc{
      opacity: .6;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .team-group{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: .5em;
    }
  }

  .team-card{
    font-size: .625em;
    &__side{
      height: 23.375em;
    }
    &__item{
      span{
        font-size: .75em;
      }
    }
  }

  .vacancies{
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }

  .vacancies-slider{
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: 100%;
        gap: 1em;
        transform: unset !important;
      }
      &-slide{
        width: 100% !important;
        padding: 0;
      }
    }
  }
}
