@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.81em;
    height: 3.5em;
    border-radius: 6.25em;
    font-weight: 600;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 150%;
    }
}

.m-btn-secondary,
.m-btn-primary{
    background-color: $primary;
    color: #FFF;
    &:disabled{
        background-color: $stroke;
        color: $grey80;
        pointer-events: none;
    }
}

.m-btn-secondary{
    &:hover{
        background-color: $stroke;
        color: $black;
    }
}

.m-btn-primary{
    &:hover{
        background-color: #F17328;
        color: #FFF;
    }
}

.m-btn-round{
    padding: 0;
    width: 3.5em;
    border-radius: 50%;
    svg,
    img{
        width: 1.5em;
        height: auto;
    }
}

.arrow-btn{
    display: flex;
    align-items: center;
    font-weight: 600;
    font-family: $font2;
    color: $black;
    &:hover{
        color: $primary;
        .arrow-btn__icon{
            background-color: $primary;
            path{
                stroke: #fff;
            }
        }
    }
    &__title{
        font-size: 1em;
        line-height: 150%;
    }
    &__icon{
        transition: inherit;
        width: 2.5em;
        height: 2.5em;
        margin-left: .75em;
        flex-shrink: 0;
        border: 1px solid $primary;
        @include center;
        border-radius: 50%;
        svg{
            width: 1.25em;
            height: auto;
        }
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 2.75em;
        span{
            font-size: .875em;
        }
    }

    .m-btn-round{
        padding: 0;
        width: 2.25em;
        height: 2.25em;
    }

    .arrow-btn{
        &__title{
            font-size: .875em;
        }
        &__icon{
            width: 2em;
            height: 2em;
        }
    }
}
