.main{
  background: $gradientDesktop;
  position: relative;
  color: #FFF;
  &:before{
    background-image: url(../img/main-bg.webp);
  }
  &__inner{
    min-height: 48.625em;
    padding: 12.56em 0 6.94em;
    display: grid;
    grid-template-columns: 46.125em calc(100% - 47.375em);
    gap: 1.25em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    color: rgba(255, 255, 255, 0.65);
    span{
      color: #fff;
    }
  }
  &__desc{
    margin-top: 3em;
    max-width: 34em;
  }
}

.m-section{
  padding: 6em 0;
}

._white-bg{
  background-color: #fff;
}

.work-for{
  overflow: hidden;
  &:before{
    background-image: url(../img/work-for-bg.webp);
  }
  &__top{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1em));
    gap: 2em;
    align-items: center;
  }
}

._slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.work-for-slider{
  margin-top: 2.5em;
  .swiper-slide{
    width: 26em;
    padding-right: 1em;
  }
}

.work-card{
  border-radius: 0.625em;
  background-color: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__top{
    padding: 1.875em;
  }
  &__icon{
    width: 2.75em;
    height: 2.75em;
    background-color: $bg;
    border-radius: 50%;
    @include center;
    margin-bottom: .75em;
    img{
      width: 1.5em;
    }
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__bottom{
    margin-top: auto;
    position: relative;
  }
  &__img{
    @include imgCover(63.75%);
    border-radius: .625em;
  }
  &__link{
    position: absolute;
    right: 1.875em;
    top: 0;
    transform: translateY(-50%);
    width: 3em;
    height: 3em;
  }
}

.upcoming-event{
  &__inner{
    margin-top: 2.5em;
    padding: 2.5em 3.88em 2.5em 5em;
    border-radius: 0.625em;
    background-color: $blue20;
    display: grid;
    grid-template-columns: calc(100% - 27.75em) 24em;
    gap: 3.75em;
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__text{
    margin-top: 2.38em;
    @include grid100gap(1.25em);
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2em;
  }
}

.m-badge{
  display: inline-flex;
  align-items: center;
  height: 2.75em;
  background-color: #fff;
  backdrop-filter: blur(46.5px);
  border-radius: 3.9375em;
  padding: 0 .875em;
  img{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .5em;
  }
}

.event-speaker{
  position: relative;
  &__img{
    display: block;
    width: 100%;
    border-radius: .625em;
  }
  &__info{
    position: absolute;
    bottom: .69em;
    left: 50%;
    transform: translateX(-50%);
    width: calc(100% - 2em);
    border-radius: 0.5em;
    background: rgba(255, 255, 255, 0.65);
    backdrop-filter: blur(17.5px);
    padding: .625em 1em;
  }
  &__prof{
    font-weight: 500;
    opacity: .6;
  }
}

.events{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .tabs{
    margin-top: 1.375em;
  }
  &__content{
    margin-top: 2.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1.875em 1em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 3.75em;
  }
}

.tabs{
  display: flex;
  flex-wrap: wrap;
}

.tab{
  display: flex;
  align-items: center;
  padding: 0 1.0625em;
  height: 2.25em;
  border-radius: 1.875em;
  border: 1px solid $grey20;
  font-weight: 500;
  margin: .5em .625em 0 0;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: .875em;
    line-height: 130%;
  }
  &:hover,
  &._active{
    background-color: $black;
    border-color: $black;
    color: #fff;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.event-card{
  &__img{
    display: block;
    border-radius: .625em;
    @include imgCover(56.15%);
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.25em;
  }
  &__tag{
    display: flex;
    align-items: center;
    padding: 0 1em;
    border-radius: 1.875em;
    background-color: $grey10;
    height: 1.875em;
    font-weight: 500;
  }
  &__date{
    font-weight: 500;
    color: $grey80;
  }
  &__title{
    margin-top: .75em;
  }
}


@media screen and (max-width: 992px){
  .main{
    background: $gradientMob;
    &__inner{
      padding: 10em 0 4em;
      min-height: unset;
      grid-template-columns: 29em calc(100% - 30.25em);
      gap: 1.25em;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .m-section{
    padding: 2.5em 0;
  }

  .work-for{
    &__top{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .work-for-slider{
    margin-top: 1.875em;
    .swiper-slide{
      width: 20.9375em;
      padding-right: 1.25em;
    }
  }

  .work-card{
    &__top{
      padding: 1.25em 1.25em 2.25em;
    }
    &__icon{
      margin-bottom: 1em;
    }
    &__desc{
      margin-top: .625em;
    }
    &__link{
      right: 1.25em;
      img{
        width: 1.25em;
        transform: rotate(45deg);
      }
    }
    &__img{
      padding-bottom: 57.1%;
    }
  }

  .upcoming-event{
    &__inner{
      margin-top: 1.25em;
      padding: 1.25em;
      grid-template-columns: calc(100% - 23em) 21em;
      gap: 1em 2em;
    }
    &__text{
      margin-top: 1.25em;
      gap: .625em;
    }
  }

  .m-badge{
    height: 2.5em;
    span{
      font-size: 1em;
    }
    img{
      width: 1.25em;
    }
  }

  .events{
    .tabs{
      margin-top: .75em;
    }
    &__content{
      margin-top: 1.875em;
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50%  - .5em));
      gap: 1.25em 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .tabs{
    width: calc(100% + .25em);
  }

  .tab{
    margin-right: .25em;
    padding: 0 1em;
  }
}


@media screen and (max-width: 576px){
  .main{
    &:before{
      background-image: url(../img/main-bg2.webp);
    }
    &__inner{
      padding: 7.06em 0 2em;
      grid-template-columns: 100%;
    }
    &__title{
      span{
        display: block;
        line-height: inherit;
      }
    }
    &__img{
      order: -1;
    }
  }

  .upcoming-event{
    &__inner{
      grid-template-columns: 100%;
      position: relative;
      padding-bottom: 5.25em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 1.25em;
      margin: 0;
      width: 100%;
      .m-btn{
        width: 100%;
      }
    }
  }

  .event-speaker{
    &__info{
      width: calc(100% - 1.25em);
    }
    &__title{
      font-size: 1em;
    }
    &__prof{
      font-size: .875em;
    }
  }

  .events{
    &__top{
      .arrow-btn{
        display: none;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      .event-card{
        &:last-child{
          border-bottom: none;
          padding-bottom: 2.125em;
          .event-card__date{
            bottom: 0;
          }
        }
      }
    }
  }

  .event-card{
    padding-bottom: 3.125em;
    border-bottom: 1px solid $stroke;
    position: relative;
    &__img{
      padding-bottom: 53.7%;
    }
    &__top{
      margin-top: .625em;
    }
    &__date{
      position: absolute;
      bottom: 1.25em;
      left: 0;
    }
    &__title{
      margin-top: .375em;
    }
  }
}
