.page-top{
    padding: 1.875em 0 2.5em;
    background: $primary;
    color: #fff;
    &:before{
        background-image: url(../img/page-top-bg.webp);
        background-position: right top;
    }
    .breadcrumbs{
        @include lastM(0 0 2.31em);
    }
}

.breadcrumbs {
    &__item {
        position: relative;
        font-weight: 500;
        display: inline;
        color: rgba(#fff,.5);
        &:last-child {
            color: #fff;
        }
        &:not(:first-child) {
            margin-left: .375em;
            &:before {
                content: '/';
                margin-right: .625em;
                display: inline-block;
                transform: translateY(5%);
            }
        }
        &:before,
        span{
            font-size: .875em;
            line-height: 143%;
        }
    }
}



@media screen and (max-width: 992px){
    .page-top{
        padding: 1.25em 0 3.81em;
        &:before{
            background-image: url(../img/page-top-bg2.webp);
        }
        .breadcrumbs{
            @include lastM(0 0 3.94em);
        }
    }
}
