@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._bg{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: 0 0;
  }
  .container{
    position: relative;
    z-index: 2;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    position: static;
    margin: 0 0 0 .5em;
  }
}

.swiper-button{
  width: 3.5em;
  height: 3.5em;
  border: 1px solid $stroke;
  border-radius: 50%;
  @include center;
  &:hover{
    background-color: $primary;
    border-color: $primary;
    path{
      fill: #fff;
    }
  }
  svg{
    width: 1.5em;
    height: auto;
  }
}
