.header{
  position: relative;
  z-index: 3;
  &__bottom,
  &__top{
    padding: 1.12em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__partners{
    display: flex;
    align-items: center;
    &-item{
      height: 2.375em;
      @include lastM(0 1.25em 0 0);
      img{
        height: 100%;
        display: block;
      }
      ._view2{
        display: none;
      }
    }
  }
  &__logo{
    display: block;
    width: 2.875em;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__bottom{
    padding-bottom: .81em;
    border-top: 1px solid #E9E7E4;
  }
  &._index{
    color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    .header__bottom{
      border-color: rgba(255, 255, 255, 0.18);
    }
    .header__partners{
      &-item{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    .lang__show,
    .header-search__btn,
    .header-user__btn{
      path{
        fill: #fff;
      }
    }
    .lang{
      .m-dropdown{
        color: $black;
      }
    }
    ._color-rect{
      fill: #fff;
    }
    .lang__show,
    a{
      &:hover{
        color: $primary50;
      }
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  .header-search,
  .lang{
    margin-left: 1.875em;
  }
}

.lang{
  z-index: 2;
  position: relative;
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
    svg{
      width: 1.5em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  .m-dropdown{
    margin-top: .25em;
    padding: .5em;
    border-radius: 1em;
    &__option{
      height: 2.5em;
      padding: 0 2em;
      border-radius: .75em;
    }
  }
  &._open{
    .m-dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.m-dropdown{
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.10), 0px 27px 27px 0px rgba(0, 0, 0, 0.09), 0px 61px 37px 0px rgba(0, 0, 0, 0.05), 0px 109px 44px 0px rgba(0, 0, 0, 0.01), 0px 171px 48px 0px rgba(0, 0, 0, 0.00);
  top: 100%;
  left: 0;
  margin-top: 1em;
  padding: 1em;
  border-radius: 1.875em;
  background: #FFF;
  min-width: 100%;
  z-index: 3;
  max-height: 32em;
  overflow-y: auto;
  @include scroll;
  &__option{
    display: flex;
    align-items: center;
    min-height: 4.25em;
    padding: .5em 1.25em;
    border-radius: 1.25em;
    font-weight: 500;
    cursor: pointer;
    transition: all .2s;
    &._selected{
      background-color: $primary;
      color: #fff;
    }
    &:hover{
      background-color: $primary10;
      color: $primary;
    }
  }
}

.header-search{
  &__btn{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.header-user{
  &__btn{
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      height: auto;
      flex-shrink: 0;
      margin-right: .5em;
    }
    &:hover{
      color: $primary;
    }
  }
}

.menu{
  &__top{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 1em;
    line-height: 150%;
    font-weight: 500;
    @include lastM(0 2.52em 0 0);
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &__top{
      padding: 0;
    }
    &__bottom{
      padding: .5em 0 0;
      margin-top: .5em;
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
    &._index{
      .burger{
        background: rgba(255, 255, 255, 0.16);
      }
      .menu{
        background: $primary;
        &__top{
          border-color: #fff;
        }
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    height: $vh;
    width: 100%;
    background: $primary20;
    overflow-y: auto;
    z-index: 21;
    padding: 1.25em;
    transition: all .5s;
    transform: translateX(-120%);
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 1em;
      border-bottom: 1px solid $primary;
    }
  }

  .burger{
    display: flex;
    svg{
      width: 1.5em;
      height: auto;
    }
  }

  .nav{
    margin-top: 1em;
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1em);
    }
  }

  .m-dropdown{
    border-radius: 1em;
    margin-top: .25em;
    padding: .5em;
    &__option{
      min-height: 3em;
    }
  }
}



@media screen and (max-width: 576px){
  .header{
    &__partners{
      &-item{
        height: 1.6875em;
        @include lastM(0 .75em 0 0);
      }
    }
    &__logo{
      width: 2.375em;
    }
  }

  .header-action{
    .lang,
    .header-search{
      display: none;
    }
  }

  .header-user{
    &__btn{
      img{
        width: 1.25em;
        margin-right: .25em;
      }
    }
  }
}
